  text-rendering: optimizeLegibility;
  font-family: "BloggerSans", helvetica, arial, sans-serif;
  font-style: normal;
  margin-top: 0; }

ul,
li {
  margin: 0; }

h1 {
  line-height: 1.44; }

.btn {
  display: block;
  position: relative;
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  text-decoration: none;
  color: #1d2e5b;
  background-color: #ffd74b;
  border-radius: 10px;
  border: 0;
  padding: 25px; }
  .btn::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 15px;
    border: 7px solid transparent;
    border-left: 10px solid #1d2e5b;
    transform: translateY(-50%); }

.page-main__section {
  position: relative;
  padding: 30px 25px 10px; }

.contact-list {
