  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  list-style: none;
  padding-left: 0; }
  .contact-list__link {
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 50%; }
    .contact-list__link--phone {
      background-color: #ff8d30; }
    .contact-list__link--mail {
      background-color: #ffbd30; }
  .contact-list--login {
    display: flex;
    flex-grow: 1;
    width: 100%; }
  .contact-list__login {
    border-radius: 50%;
    padding: 10px;
    color: #ffffff;
    background-color: #ffd74b; }

.caption {
  z-index: 1;
  color: #ffffff;
  background-color: #161c35;
  padding-bottom: 115px; }

.steps {
  z-index: 4;
  margin-top: -95px;
  padding-top: 0;
  padding-bottom: 0; }

.steps-list {
  list-style: none;
  margin: 0;
  padding: 0;
  counter-reset: list;
  color: #161c35;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px; }
  .steps-list__item {
    position: relative;
    border-radius: 10px 10px 0 0;
    background-color: #ffd74b;
    padding: 25px 50% 35px 25px;
    margin-bottom: -10px; }
    .steps-list__item::after {
      counter-increment: list;
      content: counter(list);
      position: absolute;
      text-align: right;
      right: 25px;
      top: 25px;
      color: #ffffff; }
    .steps-list__item:first-child {
      background-color: #ff8d30; }
    .steps-list__item:nth-child(2) {
      background-color: #ffa530; }
    .steps-list__item:nth-child(3) {
      background-color: #ffbd30; }
    .steps-list__item:last-child {
      border-radius: 10px;
      padding-bottom: 25px; }

.about-us {
  position: relative;
  color: #ffffff;
  z-index: 3;
  background-color: #192144;
  margin-top: -280px;
  padding-top: 310px;
  border-radius: 0 0 30px 30px;
  min-height: 350px; }
  .about-us::before {
    content: "";
    position: absolute;
    z-index: -2;
    left: 0;
    bottom: 220px;
    width: calc(130px);
    height: 130px;
    border-radius: 0 20px 20px 0;
    background-color: #141b3b; }
  .about-us::after {
    content: "";
    position: absolute;
    z-index: -1;
    left: center;
    bottom: 0;
    background: no-repeat 0 0;
    background-size: auto;
    background-size: 100% 100%;
    width: 290px;
    height: 350px;
    background-image: url(../img/about-mobile@1x.png); }

.about-us__wrapper {
  padding-top: 45px;
  padding-left: 30px;
  padding-right: 30px; }

.about-us__title {
  margin: 0;
  margin-bottom: 25px;
  color: #ff8d30; }

.about-us__text {
  font-weight: 100;
  line-height: 1.3;
  margin: 0;
  margin-bottom: 25px; }

.about-us__text--bold {
  font-weight: 700;
