    margin-bottom: 60px; }

.routes-list {
  padding: 0;
  list-style: none;
  margin-bottom: 25px; }
  .routes-list__item {
    position: relative;
    margin-bottom: 10px;
    overflow: hidden; }
    .routes-list__item:last-child {
      margin-bottom: 0; }

.route-card {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-radius: 10px; }
  .route-card__inner {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: top;
    flex-wrap: wrap;
    width: 100%;
